@mixin row($no-padding: false, $gutter: $gutter-width) {
    @include clrfx;

    $calc-gutter: - $gutter / 2;
    @if $no-padding {
        $calc-gutter: 0; }

    margin-left: $calc-gutter;
    margin-right: $calc-gutter;

    @include smartphone {
        margin-left: 0;
        margin-right: 0; } }


@mixin col ($span, $gutter: $gutter-width) {
    box-sizing: border-box;
    display: block;
    float: left;
    width: percentage(100 / 12 * $span / 100);
    padding-left: $gutter / 2;
    padding-right: $gutter / 2;

    @include smartphone {
        width: 100%;
        padding-left: 0;
        padding-right: 0; } }

