@charset "utf-8";

@import "standby.sass";

.twoCol {
    @include twoCol;

    &__fluid {
        @include twoColFluid; }

    &__fixed {
        @include twoColFixed;

        &--right {
            @include twoColFixedRight; } } }


.row {
    @include row;

    @for $i from 1 through 12 {
        > .col#{$i} {
            @include col($i); } } }
