.u {
    &-only {
        &--pc {
            @include tablet {
                display: none !important; } }

        &--sp {
            display: none !important;
            @include smartphone {
                display: initial !important; } } }


    &-not {
        &--pc {
            display: none !important;
            @include tablet {
                display: initial !important; } }

        &--sp {
            @include smartphone {
                display: none !important; } } }


    &-inner {
        &--tablet {
            @include tablet {
                margin: {
                    left: $tablet-inner-margin;
                    right: $tablet-inner-margin; } } }

        &--sp {
            @include smartphone {
                margin: {
                    left: $sp-inner-margin;
                    right: $sp-inner-margin; } } } } }
